.list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 16px;
  font-weight: 700;
}

.list__item {
  display: flex;
  align-items: center;
}

.text {
  margin: 0;
  padding: 10px;
  flex-basis: calc(100% / 3);
}

.button {
  display: block;
  margin: 0 auto;
  cursor: pointer;
  padding: 5px;
  border: 1px solid black;
  border-radius: 10px;
  font-size: 16px;
}
